@import "../../assets/variables.scss";

.roadmap-container {
  display: flex;
  flex-direction: column;
  align-items: center;
  margin-top: 80px;
  height: 800px;

  &__header {
    @include header;

    &__title {
      @include title;
      margin-bottom: 30px;
    }

    &__subtitle {
      text-align: center;
      font-weight: var(--font-weight-thin);
      font-size: var(--font-size-text-variant);
      width: 80%;
    }
  }
}
